.toolbar-container {
    display: grid;
    position: relative;
    flex-direction: row;
    justify-content: center;
    background-color: #282c34;
    height: 100px;
    width: 100%;
}
.main-logo {
    font-family: Arial, sans-serif;
    font-size: 1.7rem;
    color: white;
    font-weight: 700;
    padding: 16px 21px;
    margin: 0px 20px 0px 20px;
}

.main-message {
    display: none;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    font-family: Arial, sans-serif;
    font-size: 20px;
    line-height: 34px;
    color: white;
    padding: 15px 25px 15px 25px;
}


.toolbar-btn {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: rgb(255, 255, 255);
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
    cursor: pointer;
    outline: none;
}

.toolbar-btn:hover {
    color: rgb(164, 96, 228);
}

.toolbar-btn-running {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: rgb(182, 182, 182);
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
}

.generate-btn {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: rgb(255, 255, 255);
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
    cursor: pointer;
    background-color: blueviolet;
    border-radius: 4px;
    outline: none;
}

.generate-btn:hover {
    background-color: rgba(137, 43, 226, 0.7);
}

.generate-btn-running {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: rgb(182, 182, 182);
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
    background-color: #282c34;    
    border-radius: 4px;
    outline: none;
}

.stop-btn-running {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: white;
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
    cursor: pointer;
    background-color: rgba(226, 43, 43, 0.6);
    border-radius: 4px;
    outline: none;
}

.stop-btn-running:hover {
    background-color: rgba(226, 43, 43, 0.75);
}

.stop-btn {
    position: absolute;
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: transparent;
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
    background-color: transparent;    
    border-radius: 4px;
    outline: none;
}

.button {
    font-weight: bold;
    background-color: #282c34;
    border: none;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    outline: none;
}
